import React, { Component } from 'react';

import blueBalloon from './images/Blue.png';
import darkBlueBalloon from './images/Dark_blue.png';
import grayBalloon from './images/Gray.png';
import lightBlueBalloon from './images/Light_Blue.png';


class HoverButton extends Component {
  constructor(props) {
    super(props);
    this.name = "default";
    this.url = "default"

    this.handleMouseHover = this.handleMouseHover.bind(this);
    this.state = {
      isHovering: false,
      img: grayBalloon,
    };
  }

  handleMouseHover() {
    this.setState(this.toggleHoverState);
    this.toggleColor();
  }

  toggleColor() {
    if (this.state.img === grayBalloon) {
      this.setState((state) => ({ img: blueBalloon}));
    } else {
    this.setState((state) => ({ img: grayBalloon}));
    }
  }

  toggleHoverState(state) {
    return {
      isHovering: !state.isHovering,
    };
  }

  render() {
    return (
      <div>
        <div

        >
            <button style={{background:"transparent", border:"none"}}><img  src={this.state.img} alt={"poop"} style={{width: 162.6, height:450}}  onMouseEnter={this.handleMouseHover}         onMouseLeave={this.handleMouseHover}    /> </button>
        </div>

      </div>
    );
  }
};

export default HoverButton;

