import React, { Component } from 'react';
import FirstChild from './firstChild';

class ParentComponent extends Component {

  constructor(props) {
    super(props);
    this.state = {
      counter: 0,
      l: ["first", "second", "third"],
      text: "first"
    };
     this.increment = this.increment.bind(this);
  }

  render() {
    return(
      <div>
        <h2>Projects:</h2>
        <FirstChild text={this.state.text} />
        <button onClick={this.increment}>Next project!</button>
      </div>
    )
  }

  increment() {
    if (this.state.counter === this.state.l.length - 1) {
      this.setState((state) => ({ counter: 0}));
    } else {
    this.setState((state) => ({ counter: state.counter + 1}));
    }

    this.setState((state) => ({ text: state.l[state.counter]}))

  }
}

export default ParentComponent;
